button[data-start] {
  margin-left: 38%;
  margin-right: 60px;
  display: inline-block;
}

button {
  opacity: .8;
  width: 140px;
  color: #fff;
  cursor: pointer;
  background-color: brown;
  border: none;
  border-radius: 4px;
  margin-top: 200px;
  padding: 20px 40px;
  font-size: 26px;
  transition: all .3s ease-in-out;
}

button:hover {
  opacity: 1;
  transform: scale(1.05);
}

button:disabled {
  opacity: .25;
}

